body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  color: #000000; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

* {
  box-sizing: border-box; }

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding-right: 15px;
  padding-left: 15px; }

.button {
  display: inline-block;
  width: 170px;
  height: 54px;
  padding-top: 15px;
  vertical-align: middle;
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 800;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  box-shadow: none;
  appearance: none;
  cursor: pointer; }

.button--red {
  background-color: #e5493a;
  transition: all 0.2s linear; }

.button--red:hover,
.button--red:focus {
  background-color: #d02b1c; }

.button--red:active {
  background-color: #ba2719; }

.button--border {
  border: 2px solid #ffffff;
  transition: all 0.2s linear; }

.button--border:hover,
.button--border:focus {
  border-color: #d02b1c; }

.button--border:active {
  border-color: #8d1d13; }

.header {
  background-color: #252525;
  border-bottom: 4px solid #e5493a; }

.header__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 55px;
  padding-bottom: 55px; }

.header__logo {
  transition: all; }

.header__logo:hover,
.header__logo:focus {
  opacity: 0.8; }

.header__logo:focus {
  opacity: 0.6; }

.header__logo img {
  display: block;
  width: 100px;
  height: 41px; }

.header__nav {
  max-width: 560px; }

.header__menu-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0; }

@media (max-width: 768px) {
  .header__inner {
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 25px;
    padding-bottom: 25px; }
  .header__logo {
    margin-bottom: 20px; } }

.menu-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none; }

.menu-list__item {
  margin-right: 35px; }

.menu-list__item:last-child {
  margin-right: 0; }

.menu-list__link {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #707070;
  outline: none;
  transition: all 0.2s linear; }

.menu-list__link:hover,
.menu-list__link:focus {
  color: #e5493a; }

.menu-list__link:active {
  color: rgba(229, 73, 58, 0.7); }

.menu-list__link::before {
  content: "";
  display: block;
  width: 8px;
  height: 2px;
  margin-bottom: 2px;
  background-color: #e5493a;
  opacity: 0;
  transition: all 0.2s linear; }

.menu-list__link:hover::before,
.menu-list__link:focus::before {
  opacity: 1; }

.menu-list__link:active::before {
  opacity: 0.7; }

@media (max-width: 768px) {
  .menu-list__item {
    margin-right: 20px; } }

@media (max-width: 505px) {
  .menu-list {
    justify-content: space-between; }
  .menu-list__item {
    flex-basis: 50%;
    margin-right: 0;
    margin-bottom: 3px;
    text-align: center; }
  .menu-list__link::before {
    display: none; } }

.hero {
  background-color: #252525;
  background-image: url("../img/bg-man.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top; }

.hero__inner {
  padding-top: 255px;
  padding-bottom: 250px; }

.hero__content {
  max-width: 580px;
  width: 100%;
  margin-left: auto;
  font-family: "Raleway", "Arial", sans-serif;
  text-transform: uppercase; }

.hero__title {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 80px;
  font-weight: 800;
  line-height: 80px;
  color: #ffffff; }

.hero__title-text {
  display: block;
