  margin-bottom: 20px; }

.hero__button:last-child {
  margin-right: 0; }

@media (max-width: 1200px) {
  .hero {
    background-image: none; }
  .hero__inner {
    padding-top: 70px;
    padding-bottom: 50px; }
  .hero__content {
    max-width: none;
    width: 100%;
    margin: 0 auto;
    text-align: center; } }

@media (max-width: 650px) {
  .hero__title {
    font-size: 50px;
    line-height: 50px; }
  .hero__title-text {
    font-size: 25px;
    line-height: 25px; }
  .hero__text {
    font-size: 16px;
    line-height: 16px; } }

@media (max-width: 505px) {
  .hero__inner {
    padding-top: 30px;
    padding-bottom: 30px; }
  .hero__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .hero__title {
    font-size: 40px;
