.advantages {
  background-color: #252525; }

.advantages__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, auto));
  grid-column-gap: 30px;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 60px;
  padding-bottom: 60px;
  padding-left: 0;
  list-style: none; }

.advantages__list-item {
  margin-top: 15px;
  margin-bottom: 15px; }

.advantages__list-item:hover .advantages__img {
  border-color: #e5493a; }

.advantages__list-item:hover .advantages__img svg {
  fill: #e5493a; }

.advantages__list-item:hover .advantages__list-title {
  color: #e5493a; }

.advantages__list-item:hover .advantages__list-text {
  color: #ffffff; }

.advantages__img {
  position: relative;
  width: 104px;
  height: 104px;
  border: 1px solid #363636;
  border-radius: 50%;
  transition: all 0.2s linear; }

.advantages__img svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 40px;
  fill: #363636;
  transition: all 0.2s linear; }

.advantages__list-title {
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 800;
  line-height: 20px;
  color: #707070;
  text-transform: uppercase;
  transition: all 0.2s linear; }

.advantages__list-text {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 20px;
  color: #555555;
  transition: all 0.2s linear; }

.advantages__list-text::after {
  content: "";
  display: block;
  width: 70px;
  height: 2px;
  margin-top: 25px;
  background-color: #e5493a; }

@media (max-width: 480px) {
  .advantages__list {
    padding-top: 30px;
    padding-bottom: 25px; }
  .advantages__list-item {
    text-align: center; }
  .advantages__img {
    margin-right: auto;
    margin-left: auto; } }

.works {
  background-color: #202020; }

.works__inner {
  padding-top: 80px;
  padding-bottom: 160px; }

.works__list {
  display: grid;
  grid-template-columns: repeat(12, minmax(60px, auto));
  grid-auto-rows: minmax(270px, auto);
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

.works__list-item {
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.works__list-item--active::before {
  z-index: 1;
  opacity: 1; }

.works__list-item--active .works__list-info {
  z-index: 2;
  border-color: #e5493a;
  opacity: 1; }

.works__list-item:nth-child(2) {
  background-image: url("../img/img1.jpg"); }

.works__list-item:nth-child(3) {
  background-image: url("../img/img2.jpg"); }

.works__list-item:nth-child(4) {
  background-image: url("../img/img3.jpg"); }

.works__list-item:nth-child(5) {
  background-image: url("../img/img4.jpg"); }

.works__list-item:nth-child(6) {
  background-image: url("../img/img5.jpg"); }

.works__list-item:first-child::before {
  display: none; }

.works__list-item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(17, 17, 17, 0.85);
  opacity: 0;
  transition: all 0.2s linear; }

.works__list-item:hover .works__list-info {
  z-index: 1;
  border-color: #e5493a;
  opacity: 1; }

.works__list-item:hover::before {
  z-index: 1;
  opacity: 1; }

.works__list-item--three-columns {
  grid-column: span 3; }

.works__list-item--four-columns {
  grid-column: span 4; }

.works__list-item--five-columns {
  grid-column: span 5; }

.works__list-item--six-columns {
  grid-column: span 6; }

.works__list-title {
  position: relative;
  max-width: 230px;
  width: 100%;
  margin-top: 25px;
  margin-bottom: 25px;
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 30px;
  font-weight: 700;
  line-height: 28px;
  text-transform: uppercase;
  color: #ffffff; }

.works__list-title::before {
  content: "";
  position: absolute;
  top: -25px;
  left: 0;
  display: block;
  width: 70px;
  height: 2px;
  background-color: #2f2f2f; }

.works__list-text {
  max-width: 260px;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #464646; }

.works__list-info {
  position: relative;
  z-index: -1;
  width: 100%;
  height: 100%;
  padding-top: 10px;
  padding-left: 25px;
  padding-bottom: 80px;
  padding-right: 25px;
  border: 5px solid transparent;
  opacity: 0;
  transition: all 0.2s linear; }

.works__list-name {
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  color: #ffffff; }

.works__list-description {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  color: #959595; }

.works__list-description::after {
  content: "";
  display: block;
  width: 70px;
  height: 2px;
  margin-top: 20px;
  background-color: #e5493a; }

.works__list-btns {
  position: absolute;
  right: 30px;
  bottom: 30px; }

.works__list-zoom, .works__list-link {
  position: relative;
  display: inline-block;
  width: 42px;
  height: 42px;
  vertical-align: middle;
  margin-top: 0;
  margin-right: 2px;
  margin-bottom: 0;
  margin-left: 2px;
  padding: 0;
  border: none;
  border-radius: 50%;
  background-color: #252525;
  appearance: none;
  cursor: pointer;
  transition: all 0.2s linear; }

.works__list-zoom:hover,
.works__list-zoom:focus,
.works__list-link:hover,
.works__list-link:focus {
  background-color: #e5493a; }

.works__list-zoom:active,
.works__list-link:active {
  background-color: rgba(229, 73, 58, 0.8); }

.works__list-zoom svg, .works__list-link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 15px;
  height: 15px;
  fill: #ffffff;
  transform: translate(-50%, -50%); }

@media (max-width: 980px) {
  .works__list-title {
    font-size: 24px; }
  .works__list-name {
    font-size: 18px; } }

@media (max-width: 768px) {
  .works__list {
    grid-template-columns: repeat(2, minmax(145px, auto)); }
  .works__list-item {
    grid-column: span 1; }
  .works__list-item:first-child {
    grid-column: 1 / -1;
    max-width: 100%; }
  .works__list-title,
  .works__list-text {
    max-width: none; } }

@media (max-width: 480px) {
  .works__list {
    grid-template-columns: minmax(290px, auto);
    padding-top: 40px;
    padding-bottom: 80px; } }

